@import url(https://fonts.googleapis.com/css?family=Open+Sans:bold,normal);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* @import url('https://fonts.googleapis.com/css?family=Open+Sans&display=swap'); */

.header {
  display: flex;
  justify-content: center;
  color: #4F4F4F;
}

.current{
  text-shadow: 0 0 .65px #333, 0 0 .65px #333;
}

.hide {
  display: none;
}

body {
  background-color:#FFFFFF;
  display: flex;
  flex-direction: column;
  color: black;
  font-family: 'Open Sans';
}

.page {
  display: flex;
  flex-direction: column;
  align-items: center;
}

  .page-title {
    font-family: 'Open Sans';
    font-weight: 800;
    font-size: 60px;
    line-height: 82px;
    color: #333333;
    margin: 40px 20px 20px 20px;
    width: 900px;
  }

  .cover-title {
    margin-top: 40px;
  }

  .section-title {
    font-family: 'Open Sans';
    font-weight: bold;
    font-size: 36px;
    line-height: 24px;
  }

.impact-button {
  cursor: pointer;
  font-family: 'Open Sans';
  font-weight: bold;
  color: white;
  background: #BB6BD9;
  border: 3px dotted #FFFFFF;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.25);
  border-radius: 8px;
  font-family: 'Open Sans';
  outline: none;
}

  .impact-button-impactpage {
    color: #4F4F4F;
    background: #F2F2F2;
    border: 3px dotted #BDBDBD;
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.25);
    border-radius: 8px;
  }

button:hover {
  box-shadow: 0px 8px 8px rgba(0, 0, 0, 0.25);
}

a:link, a:visited, a:active {
  text-decoration: none;
  color: #4F4F4F;
  font-weight: normal;
}

a:hover {
  text-decoration: underline;
  color: #4F4F4F;
}



/* pollutions & comparisons */

.pollutions {
  margin-left: 20px;
  margin-right: 20px;
}

.pollution {
  margin: 20px 0px 0px 0px;
}

.impact-image {
  margin-top: 60px;
  height: 250px;
}

.same-as {
  font-style: italic;
  font-size: 20px;
  line-height: 32px;
  margin: 20px 0px 10px 0px;
}

.comparison-section {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
  
  .comparison-element {
    display: flex;
    align-items: center;
    width: 400px;
  }

    .comparison-image {
      height: 100px;
    }

    .comparison-text {
      margin-left: 10px;
    }

      .comparison {
        font-family: 'Open Sans';
        font-weight: normal;
        font-size: 20px;
        line-height: 32px;
      }

      .comparison-support {
        font-style: italic;
        font-size: 14px;
        line-height: 26px;
        color: #828282;
      }



@media only screen and (max-width: 1000px) {

  .header {
    color: #4F4F4F;
    display: flex;
    flex-direction: column;
    background-color: transparent;
    text-align: center;
  }

  .nav {
    color: #4F4F4F;
  }

  .page-title {
    width: 800px;
  }

  .impact-image {
    width: 100%;
    height: auto;
  }

}


@media only screen and (max-width: 877px) {

  .page-title {
    width: 700px;
  }

  .comparison-section {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }

    .comparison-element {
      margin-bottom: 10px;
      width: auto;
    }

      .comparison-image {
        height: 70px;
      }

}


@media only screen and (max-width: 790px) {

  .page-title {
    width: 550px;
  }

}


@media only screen and (max-width: 630px) {

  .page-title {
    font-size: 48px;
    line-height: 60px;
    width: auto;
  }

  .section-title {
    font-size: 25px;
  }

}


@media only screen and (max-width: 460px) {

  .page-title {
    font-size: 35px;
    line-height: 50px;
  }

  .section-title {
    font-size: 25px;
    line-height: 35px;
  }
  
}


@media only screen and (max-width: 350px) {

  .page-title {
    font-size: 32px;
    line-height: 45px;
  }

}
.impact-button-impactpage-form {
    margin-left: 30px;
} 
  
.impact-button-homepage-form {
    width: 100%;
}

.form-home {
    max-width: 900px;
    margin-right: 20px;
    margin-left: 20px;
    background: #F2F2F2;
    border-radius: 20px;
    padding: 20px;
}
  
.form-impact {
    margin-right: 20px;
    margin-left: 20px;
    background: #F2F2F2;
    border-radius: 20px;
    padding: 10px;
}
  
.form-title {
    padding-top: 30px;
    padding-bottom: 10px;
}

.form-support {
    font-family: 'Open Sans';
    font-weight: 300;
    font-size: 16px;
    line-height: 24px;
    padding-top: 10px;
    padding-bottom: 30px;
}

.form-input-section {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
}

    .form-input-section-impactpage {
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        flex-direction: column;
    }

    .form-input {
        font-family: 'Open Sans';
        font-weight: bold;
        border: 3px dotted #BDBDBD;
        border-radius: 8px;
        font-family: 'Open Sans';
        font-style: normal;
        font-weight: normal;
        background-color: #FFFFFF;
        font-size: 24px;
        line-height: 40px;
        padding-top: 10px;
        padding-bottom: 10px;
        margin-bottom: 15px;
    }
  
      input:focus, select:focus {
        border-color: #BB6BD9;
        outline: none;
      }
  
    .form-number {
      margin-right: 5px;
      width: 75px;
      text-align: center;
      padding-left: 15px;
    }
  
    .form-select {
      margin-left: 25px;
      -webkit-appearance: none;
         -moz-appearance: none;
              appearance: none;
      padding: 10px;
    }
  
    .form-button {
      font-size: 24px;
      line-height: 32px;
      padding: 15px 78px 15px 78px;
      margin-bottom: 20px;
    }
  
    .form-button-position-home {
      display: flex;
      flex-direction: column;
    }
    
    .form-button-position-impact {
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
      margin-top: 20px;
      margin-bottom: 25px;
      justify-content: space-around;
    }
  
  .form-image {
    width: 100%;
    height: 120px;
  }


  @media only screen and (max-width: 1000px) {

    .form-home {
        margin: 0px;
    }

  }


  @media only screen and (max-width: 877px) {

    .impact-button-impactpage-form {
      margin-left: 0px;
    }

}


@media only screen and (max-width: 460px) {

    .form-impact {
        margin-right: 10px;
        margin-left: 10px;
      }
    
    .form-input {
        border: 3px dotted #BDBDBD;
        border-radius: 8px;
        font-size: 16px;
        margin: 0px;
        margin-bottom: 10px;
        padding-top: 7px;
        padding-bottom: 7px;
    }

    .form-number {
        margin-right: 5px;
        text-align: center;
        padding-left: 4px;
    }

    .form-select {
        text-align: center;
    }

    .form-button {
        font-size: 15px;
        width: 100%;
        margin: 0px 0px 20px 0px;
        padding: 15px;
    }

    .form-button-position-impact {
        flex-direction: column;
    }

}
